import './App.css';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import {HeaderPage} from "./components/HeaderPage";
import {Users} from "./components/Users";
import {AddUser} from "./components/AddUser";

function App() {
  return (
    <>
      <Router>
        <Switch>
          <Route path="/users">
            <HeaderPage/>
            <Users/>
            <AddUser/>
          </Route>
          <Route path="/groups">
            <HeaderPage/>
          </Route>
        </Switch>
      </Router>
    </>
  )
}

export default App;
